.user-head{
    padding: 0;
}
.user-top-bar{
    display: flex;
    height: 50px;
    margin:20px 50px 0px 50px;
    align-items: center;
    justify-content: space-between;
}
.user-top-side{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.user-top-next{
    display: flex;
    height: 30px;
    justify-content: space-between;
    margin:0px 50px 20px 50px;
}
.user-data{
    border: 1px solid #efefef;
    margin:0px 50px 0px 50px;
    /* height: 450px; */
    position: relative;
    /* display: flex; */
    /* overflow: scroll; */
}
.user-data::-webkit-scrollbar{
    display: none;
}
