.signup{
    display: flex;
    /* align-items: center; */
    height: 99.7vh;
    width: 100%;
    /* border: 1px solid black; */
}
body{
    margin: 0;;
}

.left-side{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    line-height: 100%;
    color: red;
    background-color: #E0E0E0;
    text-align: center;
}
.form-main{
    height: 100%;
    width: 80%;
    /* background-color: #F26B7F; */
    background-color: #7f5a83;
    background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
    padding-top: 42px;
    padding-left: 100px;
}
.form-all{
    height: auto;
    width: 430px;
    /* border: 1px solid black; */
    padding: 40px;
}
.form-in{
    padding: 10px;
}
.form-in >input,.form-phone >input, .otp-btn{
    height: 40px;
    border: 1px solid;
    border-radius: 8px;
}
label{
    font-size: large;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: white;
}
.otp-verify{
    padding-top: 20px;
}
.otp-btn{
    width: 40%;
    /* color: red; */
    margin-left: 3px;
}
.form-phone{
    display: flex;
}
.form-submit{
    padding: 10px;
}
.form-submit> button{
    width: 100%;
    background-color: #3686BD;
}


.signup{
    left: 0;
    margin: 0;
    /* height: 100vh;
    width: 90%; */
}
.login-name{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    line-height: 100%;
    text-align: center;
}

form{
    height: 500px;
    width: 350px;
}