.sidebar{
    top: 0;
    left: 0;
    height: 100%;
    position: fixed;
    width: 320px;
    background-color:#ffffff;
    align-items: center;
    text-align: center;
    background-color: #36096d;
    background-image: linear-gradient(315deg, #36096d 0%, #37d5d6 74%);
    /* display: flex;
    flex-direction: column; */
}

.header{
    display: flex;
    padding-left: 60px;
    text-align: center;
    height: 80px;
    align-items: center;
    color: black;
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-size:0.6cm;
}


.main-data{
}
.side-data{
    padding-top: 20px;
    display: flex;
    padding-left: 40px;
    /* border: 1px solid blueviolet; */
    align-items: center;
    text-align: center;
}
.side-data:hover{
    cursor: pointer;
    color: chartreuse;
}

.main-link{
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    color: brown;
}
.link{
    color: black;
    text-decoration: none;
}
.link:hover{
    color: crimson;
    text-decoration: none;
}