.head{
    position: fixed;
    width: 100%;
    display: flex;
    height: 80px;
    background-color: #FFFFFF;
    align-items: center;
    justify-content: space-between;
    line-height: 80px;
    border: 1px solid #efefef;
}
.head-image{
    height: 60px;
    width: 60px;
}
.head-name{
    display: flex;
    align-items: center;
    padding-left: 60px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: larger;
    font-style:initial;
    font-weight: bolder;
}
.head-items{
    align-items: center;
    display: flex;
}
.items{
    padding-left: 10px;
    /* color: black; */
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: large;
    padding-right: 40px;
}
.items>a{
    color: black;
}
.items>a:hover{
    color:red;
    text-decoration: none;
}
.body{
    padding-top: 80px;
    /* height: 300px; */
    /* position: fixed; */
}
.footer{
    margin-top: 1rem;
    padding: 1rem;
    background-image: linear-gradient(to right, #8A4480,#673F8D);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
